.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #fcfcfc;
  padding: 30px 20px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

